.banner {
  height: 448px;
  position: relative;
  color: white;
  object-fit: contain;

  .banner--fadeBotton {
    height: 7.4rem;
    background-image: linear-gradient(
      180deg,
      transparent,
      rgba(37, 37, 37, 0.61),
      #111
    );
  }

  .banner__contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;

    .banner__title {
      font-style: 3rem;
      font-weight: 800;
      padding-bottom: 0.3rem;
    }
    .banner__description {
      width: 6rem;
      line-height: 1.3;
      padding-top: 1rem;
      font-size: 0.8rem;
      min-width: 360px;
      height: 80px;
    }

    .banner__buttons {
      .banner__button {
        cursor: pointer;
        color: white;
        outline: none;
        border: none;
        font-weight: 700;
        border-radius: 0.2vw;
        padding-left: 2rem;
        padding-right: 2rem;
        margin-right: 1rem;
        padding-top: 0.5rem;
        background-color: rgba(51, 51, 51, 0.5);
        padding-bottom: 0.5rem;
      }
      .banner__button:hover {
        color: #000;
        background-color: #e6e6e6;
        transition: all 0.3s;
      }
    }
  }
}
